<template>
  <div class="frame" >
    <div class="frame__header u-text-center">
        <div>
            <div @click="frontPage" class="frame__title"><h1>FeedME.IO</h1></div>
            <div class="frame__subtitle">為飢餓的你而生</div>
        </div>
        
    </div>
    <div class="content-no-padding">
        <div class="divider m-0"></div>
    </div>
    <div class="frame__body">
      <div class='content u-text-center'>
       <div class="hero fullscreen bg-yellow-200">
            <div @click="copyToMy('link')" class="hero-body">
                <div class="mx-auto">
                    <h1 class="title uppercase">看!</h1>
                    <h3 class="subtitle text-gray-600">快按這!</h3>
                </div>
            </div>
        </div>
      </div>
       
    </div>
    <div class="content-no-padding">
        <div class="divider m-0"></div>
    </div>
    <div class="frame__footer">
        <div class="frame__subtitle u-text-center"><i>這是一個開發階段的頁面</i></div>
    </div>
</div>

</template>


<script lang="ts">
import { defineComponent, reactive } from 'vue';
// import { getDatabase,ref,onValue, get, child, onChildAdded} from "firebase/database";
import { useRoute } from 'vue-router';
import router from '@/router';
import copy from 'copy-to-clipboard'
import { useToast } from "vue-toastification";
const toast = useToast()
export default defineComponent ({
    name: 'order',
    components: {
  },
    setup(){
        const route = useRoute()// vue3 get router params
        const data=reactive({
            key:route.params.keys
        })
        const  copyToMy = (type: string): void => {
        if(type === 'link'){
            copy('https://lwcjacky.github.io/FeedME.IO/#/user/' + data.key)
            toast.success("已複製到剪貼簿")
        }
        
        router.go(-1)
        }
        return{
            copyToMy,
            data
        }
    }
    
})
</script>

